@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  overflow: hidden;
  font-family: 'Quicksand', sans-serif;
}

.App {
  display: flex;
  /* justify-content: center;
  align-items: center; */
  width: 100vw;
  background-color: #ddd;
  flex-direction: row;
}

.sidebar-container{
  width: 25vw;
  height: 100vh;
}

.main{
  width: 50vw;
  height: 100vh;
  padding: 20px;
}

.button {
  padding: 10px;
  text-align: center;
  background-color: seagreen;
  color: #fff;
  border-radius: 5px;
  margin-top: 10px;
  cursor: pointer;
  
}

.sidebar{
    /* width: 100%;
    height: 100vh; */
    background-color: black;
    height: 100vh;
    overflow-y: auto;
    padding: 10px;
}
.sidebar-right{
    position: absolute;
    height: 100vh;
    background-color: #fff;
    transition: .2s linear;
    padding: 10px;
    right: -350px;
    width: 300px;
    overflow-y: auto;
}


.form-container{
    display: flex;
    flex-direction: row;
    width: 94%;
}

.form{
    display: flex;
    flex-direction: column;
    padding: 5px;
    width: 50%;
}

.form label{
    font-size: 0.9rem;
}

.form input{
    background-color: #ddd;
    padding: 7px ;
    border: 0;
    border-radius: 5px;
}


.form-25{
    display: flex;
    flex-direction: column;
    padding: 2%;
    width: 23%;
    margin-bottom: 20px;
}

.form-25 label{
    font-size: 0.8rem;
    line-height: 5;
}

.form-25 input{
    background-color: #ddd;
    padding: 7px ;
    border: 0;
    border-radius: 5px;
}

.position{
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 10px;
    margin-top: 20px;
}

